"use client";

import { useMemo, useState } from "react";
import {
  ArrowRight,
  CalendarClock,
  MessageSquare,
  Paperclip,
  Sparkles,
  X,
} from "lucide-react";
import {
  HypothesisActivity,
  HypothesisDetail,
  HypothesisStageEvent,
  HypothesisStageKey,
} from "@/lib/hypothesis-data";
import { TransitionDialog } from "./transition-dialog";
import { cn } from "@/lib/utils";

interface HypothesisDetailPanelProps {
  detail: HypothesisDetail;
  activity?: HypothesisActivity[];
  onClose?: () => void;
  onStageChange?: (hypId: string, targetStage: HypothesisStageKey, actor: string) => void;
  busy?: boolean;
}

function StageHistory({ history }: { history: HypothesisStageEvent[] }) {
  return (
    <ol className="space-y-3">
      {history.map((event) => (
        <li
          key={`${event.stage}-${event.changedAt}`}
          className="rounded-2xl border border-border/60 bg-surface-muted/60 p-3"
        >
          <div className="flex items-center justify-between text-xs text-text-secondary">
            <span className="uppercase tracking-[0.2em] text-neon-green">{event.stage}</span>
            <span>{new Date(event.changedAt).toLocaleString("ru-RU")}</span>
          </div>
          <p className="mt-1.5 text-sm font-medium text-text-primary">{event.changedBy}</p>
          {event.notes && (
            <p className="mt-1 text-sm text-text-secondary">{event.notes}</p>
          )}
        </li>
      ))}
    </ol>
  );
}

function CommentsSection({
  comments,
}: {
  comments: NonNullable<HypothesisDetail["comments"]>;
}) {
  if (comments.length === 0) {
    return (
      <p className="rounded-2xl border border-dashed border-border/60 bg-surface-hover/40 p-4 text-sm text-text-secondary/70">
        Пока комментариев нет.
      </p>
    );
  }

  return (
    <ul className="space-y-4">
      {comments.map((comment) => (
        <li key={comment.id} className="rounded-2xl border border-border/60 bg-surface-muted/60 p-4">
          <div className="flex items-center justify-between text-xs text-text-secondary">
            <span className="font-semibold text-text-primary">{comment.author}</span>
            <span>{new Date(comment.createdAt).toLocaleString("ru-RU")}</span>
          </div>
          <p className="mt-2 text-sm text-text-primary">{comment.body}</p>
          {comment.replies.length > 0 && (
            <ul className="mt-3 space-y-2 border-l border-border/40 pl-3">
              {comment.replies.map((reply) => (
                <li key={reply.id}>
                  <div className="flex items-center justify-between text-xs text-text-secondary">
                    <span className="font-medium text-text-primary">{reply.author}</span>
                    <span>{new Date(reply.createdAt).toLocaleString("ru-RU")}</span>
                  </div>
                  <p className="mt-1 text-sm text-text-primary">{reply.body}</p>
                </li>
              ))}
            </ul>
          )}
          {comment.isResolved && (
            <span className="mt-3 inline-flex items-center gap-2 rounded-full border border-[rgba(0,255,136,0.4)] bg-[rgba(0,255,136,0.08)] px-3 py-1 text-xs uppercase tracking-[0.2em] text-neon-green">
              Решено
            </span>
          )}
        </li>
      ))}
    </ul>
  );
}

export function HypothesisDetailPanel({
  detail,
  activity = [],
  onClose,
  onStageChange,
  busy = false,
}: HypothesisDetailPanelProps) {
  const [transitionOpen, setTransitionOpen] = useState(false);
  const currentStage = (detail.stage as HypothesisStageKey) ?? "IDEATION";

  const stageHistory = useMemo(
    () =>
      [...(detail.stageHistory ?? [])].sort(
        (a, b) => new Date(b.changedAt).getTime() - new Date(a.changedAt).getTime(),
      ),
    [detail.stageHistory],
  );

  const handleStageTransition = (targetStage: HypothesisStageKey) => {
    if (busy) return;
    onStageChange?.(detail.id, targetStage, detail.owner);
  };

  return (
    <div className="fixed inset-4 z-50 flex flex-col rounded-3xl border border-border/70 bg-surface/95 backdrop-blur-md shadow-[0_0_64px_rgba(0,0,0,0.45)]">
      <header className="flex items-start justify-between gap-4 border-b border-border/50 px-6 py-5">
        <div className="flex-1 min-w-0">
          <p className="text-[11px] uppercase tracking-[0.32em] text-text-secondary/60">{detail.owner}</p>
          <h2 className="mt-1.5 text-2xl font-semibold leading-tight text-text-primary">{detail.title}</h2>
          <p className="mt-2 text-sm leading-relaxed text-text-secondary">
            {detail.description}
          </p>
        </div>
        <div className="flex flex-col items-end gap-3">
          <button
            type="button"
            onClick={() => setTransitionOpen(true)}
            className={cn(
              "relative overflow-hidden rounded-full px-4 py-2 text-xs font-semibold uppercase tracking-[0.24em] text-base transition duration-200 ease-soft-spring",
              busy ? "opacity-60" : "shadow-neon hover:shadow-neon-hover"
            )}
            disabled={busy}
          >
            <span className="absolute inset-0 bg-[linear-gradient(135deg,rgba(0,255,136,0.85),rgba(64,224,208,0.85))]" />
            <span className="relative flex items-center gap-2">
              <ArrowRight className="h-4 w-4" />
              Перевести стадию
            </span>
          </button>
          <button
            type="button"
            onClick={onClose}
            className="inline-flex h-10 w-10 items-center justify-center rounded-full border border-border/60 text-text-secondary transition duration-200 hover:border-neon-green/60 hover:text-neon-green"
          >
            <X className="h-5 w-5" />
            <span className="sr-only">Закрыть просмотр</span>
          </button>
        </div>
      </header>

      <div className="flex flex-1 gap-6 overflow-hidden px-6 py-5">
        <div className="flex w-2/3 flex-col gap-5 overflow-y-auto pr-2">
          <section className="rounded-3xl border border-border/60 bg-surface-muted/60 p-4">
            <h3 className="text-sm font-semibold text-text-primary">Портфельный профиль</h3>
            <div className="mt-3 grid grid-cols-2 gap-3 text-sm text-text-secondary">
              <div>
                <p className="uppercase tracking-[0.28em] text-xs text-text-secondary/60">Стадия</p>
                <p className="mt-1 text-base font-semibold text-neon-green">{currentStage}</p>
              </div>
              <div>
                <p className="uppercase tracking-[0.28em] text-xs text-text-secondary/60">Здоровье стадии</p>
                <p className="mt-1 text-base font-semibold text-text-primary">{detail.stageHealth}</p>
              </div>
              <div>
                <p className="uppercase tracking-[0.28em] text-xs text-text-secondary/60">Impact</p>
                <p className="mt-1 text-lg font-semibold text-text-primary">
                  {Number(detail.impact ?? 0).toFixed(1)}
                </p>
              </div>
              <div>
                <p className="uppercase tracking-[0.28em] text-xs text-text-secondary/60">Feasibility</p>
                <p className="mt-1 text-lg font-semibold text-text-primary">
                  {Number(detail.feasibility ?? 0).toFixed(1)}
                </p>
              </div>
              <div>
                <p className="uppercase tracking-[0.28em] text-xs text-text-secondary/60">Confidence</p>
                <p className="mt-1 text-lg font-semibold text-text-primary">
                  {Math.round(Number(detail.confidence ?? 0) * 100)}%
                </p>
              </div>
              <div>
                <p className="uppercase tracking-[0.28em] text-xs text-text-secondary/60">Целевая ценность</p>
                <p className="mt-1 text-sm text-text-primary">{detail.targetValue ?? "—"}</p>
              </div>
            </div>
          </section>

          <section className="rounded-3xl border border-border/60 bg-surface-muted/60 p-4">
            <h3 className="flex items-center gap-2 text-sm font-semibold text-text-primary">
              <CalendarClock className="h-4 w-4 text-neon-green" />
              Хронология стадий
            </h3>
            <div className="mt-3">
              <StageHistory history={stageHistory} />
            </div>
          </section>

          <section className="rounded-3xl border border-border/60 bg-surface-muted/60 p-4">
            <h3 className="flex items-center gap-2 text-sm font-semibold text-text-primary">
              <Paperclip className="h-4 w-4 text-neon-green" />
              Вложения
            </h3>
            <ul className="mt-3 space-y-2">
              {(detail.attachments ?? []).map((attachment) => (
                <li
                  key={attachment.id}
                  className="flex items-center justify-between rounded-2xl border border-border/60 bg-surface-hover/50 px-3 py-2 text-sm text-text-secondary"
                >
                  <span className="font-medium text-text-primary">{attachment.name}</span>
                  <span>
                    v{attachment.version} · {new Date(attachment.uploadedAt).toLocaleDateString("ru-RU")}
                  </span>
                </li>
              ))}
              {(detail.attachments ?? []).length === 0 && (
                <li className="rounded-xl border border-dashed border-border/60 bg-surface-hover/40 p-4 text-center text-sm text-text-secondary/70">
                  Вложения отсутствуют
                </li>
              )}
            </ul>
          </section>
        </div>

        <aside className="flex w-1/3 flex-col gap-5 overflow-y-auto pl-2">
          <section className="rounded-3xl border border-border/60 bg-surface-muted/60 p-4">
            <h3 className="flex items-center gap-2 text-sm font-semibold text-text-primary">
              <Sparkles className="h-4 w-4 text-neon-green" />
              Лента активности
            </h3>
            <ul className="mt-3 space-y-3">
              {activity.slice(0, 6).map((event) => (
                <li key={event.id} className="rounded-2xl border border-border/60 bg-surface-hover/50 p-3 text-sm">
                  <div className="flex items-center justify-between text-xs text-text-secondary">
                    <span className="uppercase tracking-[0.24em] text-neon-green">{event.stage}</span>
                    <span>{new Date(event.occurredAt).toLocaleString("ru-RU")}</span>
                  </div>
                  <p className="mt-1 text-sm font-semibold text-text-primary">{event.title}</p>
                  {event.detail && <p className="mt-1 text-xs text-text-secondary">{event.detail}</p>}
                </li>
              ))}
              {activity.length === 0 && (
                <li className="rounded-xl border border-dashed border-border/60 bg-surface-hover/40 p-4 text-center text-sm text-text-secondary/70">
                  Активность пока не зафиксирована
                </li>
              )}
            </ul>
          </section>

          <section className="rounded-3xl border border-border/60 bg-surface-muted/60 p-4">
            <h3 className="flex items-center gap-2 text-sm font-semibold text-text-primary">
              <MessageSquare className="h-4 w-4 text-neon-green" />
              Обсуждение
            </h3>
            <div className="mt-3">
              <CommentsSection comments={detail.comments ?? []} />
            </div>
          </section>
        </aside>
      </div>

      <TransitionDialog
        open={transitionOpen}
        onOpenChange={setTransitionOpen}
        hypothesis={detail}
        currentStage={currentStage}
        onTransition={handleStageTransition}
        isSubmitting={busy}
      />
    </div>
  );
}
